@import "./variables.css";

.search-form {
  width     : 40%;
  transition: all 500ms;
}

.search-form:focus-within {
  width: 50%;
}

@media screen and (max-width: 1100px) {
  .search-form {
    width: 60%;
  }

  .search-form:focus-within {
    width: 80%;
  }
}

.input-btn-wrapper {
  width          : 100%;
  display        : flex;
  justify-content: center;
  margin-bottom  : 1em;
}

.search-input {
  font-size    : 2em;
  background   : var(--darker-color);
  outline      : none;
  border       : 1px solid var(--minor-elems-color);
  border-right : none;
  border-radius: 4px 0px 0px 4px;
  color        : white;
  padding      : 20px;
  width        : 100%;
  transition   : 300ms;
}

@media screen and (max-width: 700px) {
  .search-input {
    font-size: 1em;
    padding  : 10px;
  }
}

.search-input:focus {
  background: var(--minor-elems-color);
  color     : var(--main-color);
}

.search-btn {
  font-size    : 2em;
  color        : white;
  padding      : 20px;
  background   : var(--success-color);
  border       : 1px solid var(--success-color);
  border-radius: 0px 4px 4px 0px;
  transition   : 300ms;
}

@media screen and (max-width: 700px) {
  .search-btn {
    font-size: 1em;
    padding  : 10px;
  }
}

.search-btn:hover {
  color     : var(--success-color);
  background: none;
}
