@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap);
* {
  margin     : 0;
  padding    : 0;
  box-sizing : border-box;
  font-family: 'Roboto', sans-serif;
}

body {
  -webkit-font-smoothing : antialiased;
  -moz-osx-font-smoothing: grayscale;
}
body {
  --main-color       : #0c1017;
  --darker-color     : #080c10;
  --lighter-color    : #20252d;
  --success-color    : #009134;
  --link-color       : #429bff;
  --link-color-hover : #3575be;
  --minor-elems-color: #89939e;
}
.search-form {
  width     : 40%;
  transition: all 500ms;
}

.search-form:focus-within {
  width: 50%;
}

@media screen and (max-width: 1100px) {
  .search-form {
    width: 60%;
  }

  .search-form:focus-within {
    width: 80%;
  }
}

.input-btn-wrapper {
  width          : 100%;
  display        : -webkit-flex;
  display        : flex;
  -webkit-justify-content: center;
          justify-content: center;
  margin-bottom  : 1em;
}

.search-input {
  font-size    : 2em;
  background   : var(--darker-color);
  outline      : none;
  border       : 1px solid var(--minor-elems-color);
  border-right : none;
  border-radius: 4px 0px 0px 4px;
  color        : white;
  padding      : 20px;
  width        : 100%;
  transition   : 300ms;
}

@media screen and (max-width: 700px) {
  .search-input {
    font-size: 1em;
    padding  : 10px;
  }
}

.search-input:focus {
  background: var(--minor-elems-color);
  color     : var(--main-color);
}

.search-btn {
  font-size    : 2em;
  color        : white;
  padding      : 20px;
  background   : var(--success-color);
  border       : 1px solid var(--success-color);
  border-radius: 0px 4px 4px 0px;
  transition   : 300ms;
}

@media screen and (max-width: 700px) {
  .search-btn {
    font-size: 1em;
    padding  : 10px;
  }
}

.search-btn:hover {
  color     : var(--success-color);
  background: none;
}

@-webkit-keyframes pulse {
  0% {
    -webkit-transform: scale(0.95);
            transform: scale(0.95);
  }

  70% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }

  100% {
    -webkit-transform: scale(0.95);
            transform: scale(0.95);
  }
}

@keyframes pulse {
  0% {
    -webkit-transform: scale(0.95);
            transform: scale(0.95);
  }

  70% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }

  100% {
    -webkit-transform: scale(0.95);
            transform: scale(0.95);
  }
}

.loader {
  width          : 100%;
  display        : -webkit-flex;
  display        : flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items    : center;
          align-items    : center;
}

.loader img {
  display  : block;
  height   : 120px;
  -webkit-animation: pulse 1s infinite;
          animation: pulse 1s infinite;
}
.repo-card {
  display        : -webkit-flex;
  display        : flex;
  -webkit-flex-direction : column;
          flex-direction : column;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items    : center;
          align-items    : center;
  border         : 1px solid var(--minor-elems-color);
  border-radius  : 4px;
  width          : 300px;
  max-height     : 200px;
  margin         : 0px 0.5em
}

@media screen and (max-width: 1230px) {
  .repo-card {
    width        : 90%;
    margin-bottom: 0.5em;
  }
}

.repo-name {
  margin-top     : 0.5em;
  display        : -webkit-flex;
  display        : flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items    : center;
          align-items    : center;
}

.repo-name a {
  text-decoration: none;
  color          : var(--link-color);
  transition     : 300ms;
}

.repo-name a:hover {
  color          : #619de2;
  cursor         : pointer;
  text-decoration: underline;
}

.repo-icon {
  margin-right: 0.5em;
  display     : block;
}

.repo-description {
  padding   : 0 0.5em;
  margin-top: 0.5em;
  overflow  : auto;
}

.repo-description::-webkit-scrollbar {
  width: 4px;
}

.repo-description::-webkit-scrollbar-track {
  background: none;
}

.repo-description::-webkit-scrollbar-thumb {
  background-color: var(--minor-elems-color);
  border-radius   : 2px;
}

.repo-info {
  width          : 100%;
  display        : -webkit-flex;
  display        : flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items    : center;
          align-items    : center;
}

.repo-info-block {
  display        : -webkit-flex;
  display        : flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items    : center;
          align-items    : center;
  padding        : 0.5em;
}

.repo-info-icon {
  height      : 16px;
  width       : 16px;
  display     : block;
  margin-right: 0.5em;
}
.org-card {
  position: relative;
  margin  : 1em 0.2em 0 0.2em;
}

.org-logo {
  height: 48px;
}

.org-tooltip {
  display      : none;
  background   : var(--lighter-color);
  border       : 1px solid var(--minor-elems-color);
  border-radius: 4px;
  padding      : 0.5em;
}

.org-card:hover .org-tooltip {
  width          : 400px;
  z-index        : 10;
  display        : -webkit-flex;
  display        : flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items    : center;
          align-items    : center;
  position       : absolute;
  bottom         : calc(100% + 1em);
}

@media screen and (max-width: 460px) {
  .org-card:hover .org-tooltip {
    width: 200px;
  }
}

@media screen and (max-width: 1048px) {
  .org-card {
    position: static;
  }

  .org-card:hover .org-tooltip {
    bottom   : 100%;
    left     : 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
  }
}

.org-tooltip .logo-container {
  margin-top     : 0;
  display        : -webkit-flex;
  display        : flex;
  -webkit-flex-direction : column;
          flex-direction : column;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  -webkit-align-items    : center;
          align-items    : center;
}

.org-tooltip .logo-container img {
  display: block;
}


.org-tooltip .info-container {
  -webkit-flex-grow      : 2;
          flex-grow      : 2;
  margin-left    : 0.5em;
  display        : -webkit-flex;
  display        : flex;
  -webkit-flex-direction : column;
          flex-direction : column;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  -webkit-align-items    : flex-start;
          align-items    : flex-start;
}

.org-tooltip hr {
  width : 70%;
  margin: 0.3em 0
}

.org-tooltip p {
  color: var(--minor-elems-color);
}
.view-github-btn-container a {
  display   : block;
  box-sizing: border-box;
  margin    : 0;
  padding   : 0;
}

.view-github-btn {
  font-size    : 1em;
  padding      : 0.5em;
  background   : var(--success-color);
  color        : white;
  border       : 1px solid var(--success-color);
  border-radius: 4px;
  transition   : 300ms;
}

.view-github-btn:hover {
  color     : var(--success-color);
  background: none;
}
.user-page-conatainer {
  min-height     : 100vh;
  width          : 100%;
  color          : white;
  display        : -webkit-flex;
  display        : flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items    : center;
          align-items    : center;
}

@media screen and (max-width: 1260px) {
  .user-page-conatainer {
    -webkit-flex-direction: column;
            flex-direction: column;
    margin-bottom : 2em;
  }
}

.personal-info {
  display        : -webkit-flex;
  display        : flex;
  -webkit-flex-direction : column;
          flex-direction : column;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  -webkit-align-items    : flex-start;
          align-items    : flex-start;
  margin-left    : 3em;
}

@media screen and (max-width: 1260px) {
  .personal-info {
    -webkit-align-items  : center;
            align-items  : center;
    margin-bottom: 1em;
    margin-left  : 0;
  }
}

.personal-info div {
  margin-top: 1em;
}

@media screen and (max-width: 1260px) {
  .personal-info div {
    text-align: center;
  }
}

.user-avatar img {
  height       : 400px;
  border-radius: 50%;
  border       : 1px solid var(--minor-elems-color);
}

@media screen and (max-width: 420px) {
  .user-avatar img {
    height: 300px;
  }
}

.user-info-item {
  margin-left: 2em;
}

@media screen and (max-width: 1260px) {
  .user-info-item {
    margin-left: 0;
  }
}

.user-names h1 {
  margin: 0;
}

.user-names h2 {
  color: var(--minor-elems-color);
}

.user-follows {
  display        : -webkit-flex;
  display        : flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items    : center;
          align-items    : center;
}

.user-follows img {
  display: block;
  margin : 0 0.5em;
  height : 18px;
}

.user-follows img.dot {
  height: 8px;
}

.user-follows h4 {
  margin : 0;
  padding: 0;
}

.user-location {
  display        : -webkit-flex;
  display        : flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items    : center;
          align-items    : center;
}

.user-location img {
  display: block;
  margin : 0 0.5em;
}

.user-location h4 {
  margin : 0;
  padding: 0;
}

.user-socials a {
  color          : var(--link-color);
  text-decoration: none;
}

.user-socials a:hover {
  color          : #619de2;
  cursor         : pointer;
  text-decoration: underline;
}

.right-side-wrapper {
  display        : -webkit-flex;
  display        : flex;
  -webkit-flex-direction : column;
          flex-direction : column;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items    : center;
          align-items    : center;
  min-width      : 50%
}

.repos-container {
  display        : -webkit-flex;
  display        : flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items    : stretch;
          align-items    : stretch;
  -webkit-flex-wrap      : wrap;
          flex-wrap      : wrap;
  margin-right   : 1em;
}

.repo-wrapper {
  box-sizing     : border-box;
  margin-bottom  : 1em;
  display        : -webkit-flex;
  display        : flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items    : stretch;
          align-items    : stretch;
}

@media screen and (max-width: 1260px) {
  .repo-wrapper {
    width: 304px;
  }
}

.orgs-container {
  width          : 100%;
  padding        : 0 0.3em;
  display        : -webkit-flex;
  display        : flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-items    : center;
          align-items    : center;
  -webkit-flex-wrap      : wrap;
          flex-wrap      : wrap;
}

@media screen and (max-width: 1048px) {
  .orgs-container {
    position: relative;
  }
}
.not-found-container {
  color     : white;
  text-align: center;
}

.not-found-container a {
  color: var(--link-color);
}
.App {
  min-height     : 100vh;
  margin         : 0;
  padding        : 0;
  background     : var(--main-color);
  color          : white;
  display        : -webkit-flex;
  display        : flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items    : center;
          align-items    : center;
}
